import { Util } from "./Util";
export class UtilDOM {
    /**
     * Adds a button to the top-right of the navbar
     * @param config Button configuration
     * @param target Target element
     */
    static addSettingsButton(config, target = "menu.extra") {
        if (config.name === undefined)
            config.name = "T";
        if (config.href === undefined)
            config.href = "";
        if (config.title === undefined)
            config.title = "";
        if (config.tabClass === undefined)
            config.tabClass = "";
        if (config.linkClass === undefined)
            config.linkClass = "";
        if (config.attr === undefined)
            config.attr = {};
        const $tab = $(`<li>`).appendTo(target);
        const $link = $("<a>")
            .html(config.name)
            .attr({
            "title": config.title,
            "id": config.id,
        })
            .appendTo($tab);
        if (config.onClick !== undefined)
            $link.on("click", () => { config.onClick($link); });
        if (config.href) {
            $link.attr("href", config.href);
        }
        if (config.tabClass) {
            $tab.addClass(config.tabClass);
        }
        if (config.linkClass) {
            $link.addClass(config.linkClass);
        }
        if (config.attr) {
            $link.attr(config.attr);
        }
        return $link;
    }
    /**
     * Adds the given style to the document and returns the injected style element
     * @param css string CSS styles
     */
    static addStyle(css) {
        return $("<style>")
            .attr({
            "id": Util.ID.make(),
            "type": "text/css"
        })
            .html(css)
            .appendTo("head");
    }
    /**
     * Returns a base-64 encoded image used for placeholder during lazy loading
     */
    static getPlaceholderImage() {
        return "data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==";
    }
    static getKoFiImage() {
        return "data:image/png;base64,";
    }
    static getLoadingImage() {
        return "data:image/png;base64,";
    }
}
