import { XM } from "./XM";
export class XMUtil {
    /**
     * Open a new tab with this url.
     * @param url Page URL
     * @param options Tab options
     */
    static openInTab(path, active = true) {
        if (typeof GM === "undefined")
            XM.Chrome.execBackgroundRequest("XM", "Util", "openInTab", [path, active]);
        else
            GM.openInTab(path, { active: active, });
    }
    /**
     * Copies data into the clipboard
     * @param data Data to be copied
     * @param info object like "{ type: 'text', mimetype: 'text/plain'}" or a string expressing the type ("text" or "html")
     */
    static setClipboard(data, info) {
        if (typeof GM === "undefined")
            XM.Chrome.execBackgroundRequest("XM", "Util", "setClipboard", [data]);
        else
            GM.setClipboard(data, info);
    }
    ;
}
