import { Debug } from "../utility/Debug";
import { Util } from "../utility/Util";
const validKeys = [
    "1", "2", "3", "4", "5", "6", "7", "8", "9", "0",
    "-", "=", ".", ",", "/", ";", "'", "[", "]",
    "a", "b", "c", "d", "e", "f", "g", "h", "i", "j", "k", "l", "m", "n", "o", "p", "q", "r", "s", "t", "u", "v", "w", "x", "y", "z",
    "escape", "ctrl", "alt", "shift", "return",
    "up", "down", "left", "right",
];
const replacedKeys = {
    // event.key returns a different name to what jquery.hotkeys expects
    "enter": "return",
    "control": "ctrl",
    "arrow": "",
    // replacing shift-modified symbols with respective values
    // it makes no practical difference, but it looks neater
    "!": "1", "@": "2", "#": "3", "$": "4", "%": "5",
    "^": "6", "&": "7", "*": "8", "(": "9", ")": "0",
    "_": "-", "+": "=", "<": ",", ">": ".", "?": "//",
    ":": ";", '"': "'", "{": "[", "}": "]",
};
const replacedRegExp = Util.getKeyRegex(replacedKeys);
export class KeybindManager {
    static enable() { KeybindManager.enabled = true; }
    static disable() { KeybindManager.enabled = false; }
    static block() { KeybindManager.blocked = true; }
    static register(keybind) {
        if (KeybindManager.blocked)
            return;
        if (Array.isArray(keybind)) {
            for (const entry of keybind)
                this.register(entry);
            return;
        }
        // Create a listener if one does not exist already
        this.refreshListener(keybind.keys, keybind.element, keybind.selector);
        // Register the keybind function in the executor
        for (const key of keybind.keys) {
            if (key.length == 0)
                continue;
            this.executors.get(key)[keybind.bindMeta] = keybind;
        }
    }
    static unregister(bindMeta) {
        if (!Array.isArray(bindMeta))
            bindMeta = [bindMeta];
        for (const executor of this.executors.values())
            for (const key of Object.keys(executor))
                if (bindMeta.includes(key))
                    delete executor[key];
    }
    static record(callback) {
        KeybindManager.listening = true;
        let keys = [];
        $(document).on("keydown.re621.record", (event) => {
            const key = event.key
                .toLowerCase()
                .replace(replacedRegExp, (matched) => {
                return replacedKeys[matched];
            });
            if (validKeys.indexOf(key) == -1)
                return;
            keys.push(key);
        });
        $(document).on("keyup.re621.record", () => {
            if (keys.length !== 0) {
                $(document).off(".re621.record");
                callback(keys);
                KeybindManager.listening = false;
                return;
            }
            keys = [];
        });
    }
    static count(sequence) {
        if (!this.executors.has(sequence))
            return 0;
        return Object.keys(this.executors.get(sequence)).length;
    }
    static refreshListener(keys, element, selector) {
        for (const key of keys) {
            if (key.length == 0)
                continue;
            // Establish the listener structure
            if (!this.listeners.has(key)) {
                let keydown = false;
                this.listeners.set(key, (event) => {
                    if (KeybindManager.listening)
                        return;
                    const listenerExecutor = this.executors.get(key);
                    Debug.log(`[${key}]: triggered ${Object.entries(listenerExecutor).length} executors`);
                    for (const [bindMeta, keyObj] of Object.entries(listenerExecutor)) {
                        // If the keybind is outright disable, skip
                        if (!keyObj.enabled)
                            continue;
                        // This is a dumb solution, but it'll work for the time being
                        // The issue is that any keybind can be made holdable by binding it
                        // to a the same key as an already holdable key. This may allow
                        // people to spam certain actions.
                        if (keyObj.holdable)
                            keydown = false;
                        // Execute the keybind function
                        keyObj.fnct(event, bindMeta);
                    }
                });
                // Create the listener itself
                const $element = element ? $(element) : $(document);
                if (!selector)
                    selector = null;
                let cooldown = null;
                $element.on("keydown.re621.hotkey-" + key, selector, key, (event) => {
                    if (keydown)
                        return;
                    if (cooldown)
                        return;
                    keydown = true;
                    if (!KeybindManager.enabled || KeybindManager.listening)
                        return false;
                    Debug.log(`[${key}]: caught`);
                    this.listeners.get(key)(event);
                    // Slightly throttles the keystroke input speed
                    // Should only be an issue with holdable keybinds
                    clearTimeout(cooldown);
                    cooldown = setTimeout(() => {
                        clearTimeout(cooldown);
                        cooldown = null;
                    }, 50);
                });
                $element.on("keyup.re621.hotkey-" + key, selector, key, () => {
                    keydown = false;
                });
                $(window).on("blur", () => {
                    keydown = false;
                });
            }
            // Create the executor
            if (!this.executors.has(key))
                this.executors.set(key, {});
        }
    }
}
KeybindManager.listeners = new Map();
KeybindManager.executors = new Map();
KeybindManager.enabled = true; // if false, stops executor functions from running
KeybindManager.blocked = false; // if true, stops listener functions from being created
KeybindManager.listening = false; // same as enabled, but for internal use only
